.DestContainer{
    width: 100%;
    padding: 10px;
    text-align: center;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    align-content: stretch;
    justify-content: space-around;
}


.DestOut{
            max-width: 40%;
            height: fit-content;
            margin: 10px;
            background-color: whitesmoke;
            -webkit-box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
            -moz-box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
            box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
}
.DestOut img{
    width: 100%;
    height: 250px;
}


.DestOut h1{
        color: royalblue;
        font-weight: bolder;
}

.DestOut p{
    text-align: justify;
    padding: 2%;
}

.DestOut ul{
    list-style-type: none;
    background-color: royalblue;
    color: whitesmoke;
    height: 30px;
    padding: 1%;
    margin-bottom: 0;
}
.DestOut ul li{
    display: inline;
    padding: 1%;
}
.DestOut span.duration{
    background-color: tomato;
    width: 100%;
    display: inline-block;
}
.prices span.prix{
    font-weight: bolder;
}

.btnReserve{
    width: 100%;
    margin: 0;
    border-radius: 0;
    background-color:whitesmoke;
    color: royalblue;
}
.btnReserve:hover{
    -webkit-box-shadow: 0px 0px 16px 3px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 0px 16px 3px rgba(0,0,0,0.75);
    box-shadow: 0px 0px 16px 3px rgba(0,0,0,0.75);
}


.reserveContainer {
  text-align: center;
  position: fixed; 
  z-index: 1; 
  left: 0;
  top: 0;
  width: 100%; 
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  justify-self: center;
  justify-items: center;
  align-items: center;
  align-self: start;
  align-content: center;
  overflow: auto; 
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.chosenPlan{
    max-width: max-content;
    padding: 2%;
}
.infoperso {
    width: 40%;
    margin-top: 3%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2%;
    background-color: whitesmoke;
    padding: 2%;
    text-align: center;
    -webkit-box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
    box-shadow: 0px 2px 11px -2px rgba(0,0,0,0.75);
}

.infoperso fieldset legend{
    color: royalblue;
    font-weight: bolder;
    letter-spacing: 1px;
    font-size: 1.6em;
    margin-bottom: 10px;
}
.infoperso fieldset {
    margin-bottom: 18px;
    border: none;
}

.infoperso label{
    font-weight: bold;
    margin-top: 2%;
    margin-bottom: 0.5%;
}

.infoperso input{
    width: 300px;
    padding: 5px;
    margin-left: auto;
    margin-right: auto;
}

.btnReserver2{
    display: block;
        width: fit-content;
        padding: 10px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;
        background-color: royalblue;
        color: whitesmoke;
        font-weight: bold;
        cursor: pointer;
}
.btnReserver3{
        display: block;
        font-size: 1.6em;
        width: fit-content;
        padding: 10px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 10px;
        background-color:whitesmoke;
        color:  royalblue; 
        font-weight: bolder;
        cursor: pointer;
}
@media (max-width: 960px)
{
    .DestContainer{
        flex-direction: column;
    }
    .DestOut{
        max-width: fit-content;
    }
}