* {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body{
  background-color: lightgray;
}

.home, .error{
    width: 96%;
    min-height:100%;
    text-align: center;
    padding: 20%;
    margin: 4%;
    margin-left: auto;
    margin-right: auto;
    background:url("https://images.unsplash.com/photo-1544620347-c4fd4a3d5957?ixlib=rb-1.2.1&auto=format&fit=crop&w=749&q=80");
    background-size: cover;
}
.home h1, .error h1, .error h4{
  padding: 20px;
  background-color:rgba(65,105,225,0.5);
  color: rgba(245,245,245,1);
  font-weight: bolder;
}

.error a{
  background-color:rgba(65,105,225,0.5);
  color: rgba(245,245,245,1);
  padding: 20px;
  position: relative;
  top: 15px;
  }

